import Vue from "vue";
import VueRouter from "vue-router";
import store from "@/store/index.js";
Vue.use(VueRouter);
import VueMeta from "vue-meta";
import firebase from "firebase";
Vue.use(VueMeta, {
  // optional pluginOptions
  refreshOnceOnNavigation: true,
});
const routes = [
  {
    path: "/home",
    name: "home",
    component: () => import(/*webpackChunkName:"Home"*/ "../views/Home.vue"),
  },
  {
    path: "/login",
    name: "login",
    component: () => import(/*webpackChunkName:"Login"*/ "../views/Login.vue"),
  },
  {
    path: "/logout",
    name: "logout",
    component: () =>
      import(/*webpackChunkName:"Logout"*/ "../views/Logout.vue"),
  },
  {
    path: "/edit",
    name: "edit",
    component: () => import(/*webpackChunkName:"Edit"*/ "../views/Edit.vue"),
  },
  {
    path: "/list",
    name: "list",
    component: () => import(/*webpackChunkName:"List"*/ "../views/List.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/time",
    name: "time",
    component: () => import(/*webpackChunkName:"Time"*/ "../views/Time.vue"),
    meta: { requiresAuth: true },
  },
  {
    path: "/lens",
    name: "lens",
    component: () => import(/*webpackChunkName:"Lens"*/ "../views/Lens.vue"),
  },
  {
    path: "*",
    redirect: "/home",
  },
];

const router = new VueRouter({
  /*  mode: "history",*/
  /*base: process.env.BASE_URL,*/
  routes,
});
router.beforeEach((to, from, next) => {
  if (to.matched.some((record) => record.meta.requiresAuth)) {
    // すでに認証済みの場合は遷移
    if (firebase.auth().currentUser) {
      store.commit("onAuthStateChanged", firebase.auth().currentUser);
      next();
      return;
    }
    // まだ認証されていない場合は Firebase SDK からのコールバックを待つ
    firebase.auth().onAuthStateChanged((user) => {
      const comUser = user || {};
      store.commit("onAuthStateChanged", comUser);

      if (user) {
        next();
      } else {
        next({ name: "login" });
      }
      return;
    });
  }
  next();
});

export default router;
