<template>
  <div>
    <v-navigation-drawer v-model="drawer" color="primary" dark temporary app>
      <v-list-item>
        <v-list-item-content>
          <v-list-item-title class="title mt-5" align="center">
            Recipe
          </v-list-item-title>
        </v-list-item-content>
      </v-list-item>

      <v-divider></v-divider>

      <v-list>
        <v-list-group no-action sub-group :value="true" color="secondary">
          <template v-slot:activator>
            <v-list-item-content color="red">
              <v-list-item-title>メニュー</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item-group v-model="retindex">
            <v-list-item
              v-for="(menu, i) in menus"
              :key="i"
              link
              @click="$router.push(menu.href)"
            >
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-list-item-title class="ml-10" v-on="on">{{
                    menu.text
                  }}</v-list-item-title>
                </template>
                <span>{{ menu.message }}</span>
              </v-tooltip>
            </v-list-item>
          </v-list-item-group>
        </v-list-group>
        <v-list-group no-action sub-group :value="true" color="secondary">
          <template v-slot:activator>
            <v-list-item-content color="red">
              <v-list-item-title>ガーデン</v-list-item-title>
            </v-list-item-content>
          </template>
          <v-list-item-group v-model="retindex">
            <v-list-item link to="lens">
              <v-tooltip bottom>
                <template v-slot:activator="{ on }">
                  <v-list-item-title class="ml-10" v-on="on">
                    Lens
                  </v-list-item-title>
                </template>
                <span>ABCD行列を用いたシミュレーションです。</span>
              </v-tooltip>
            </v-list-item>
          </v-list-item-group>
        </v-list-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app color="primary" dark>
      <v-app-bar-nav-icon @click="sidebarStatus"> </v-app-bar-nav-icon>
      <v-toolbar-title>Recipe</v-toolbar-title>

      <v-spacer></v-spacer>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <div class="px-2" v-on="on">
            <v-icon dark>mdi-account-circle</v-icon>
            {{ account.username }}
          </div>
        </template>
        <v-list color="primary" dark v-if="account.username !== ''">
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-account</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="account.username" />
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-email</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="account.email" />
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-content>
              <v-img width="10%" :src="account.photoURL" />
            </v-list-item-content>
          </v-list-item>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-logout</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-btn @click="logout">logout</v-btn>
            </v-list-item-content>
          </v-list-item>
        </v-list>
        <v-list color="primary" dark v-else>
          <v-list-item>
            <v-list-item-icon>
              <v-icon>mdi-login</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-btn to="login">login</v-btn>
            </v-list-item-content>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
  </div>
</template>
<script>
import firebase from "firebase";
export default {
  data() {
    return {
      account: {
        username: "",
        email: "",
        photoURL: "",
      },
      drawer: false,
      model: {
        nav: "x",
      },
      menus: [
        {
          href: "home",
          text: "ホーム",
          message: "ホーム画面",
        },
        {
          href: "list",
          text: "編集",
          message: "編集するための一覧を開きます",
        },
        {
          href: "time",
          text: "時間管理",
          message: "時間管理します。",
        },
      ],
    };
  },
  mounted() {
    this.$store.subscribe((mutation, state) => {
      if (mutation.type === "onAuthStateChanged") {
        this.account.username = state.user.displayName || "";
        this.account.email = state.user.email || "";
        this.account.photoURL = state.user.photoURL || "";
      }
    });
  },
  methods: {
    sidebarStatus() {
      this.drawer = !this.drawer;
    },
    logout() {
      firebase.auth().signOut();
      this.$store.commit("onAuthStateChanged", {});
      this.$router.push("home");
    },
  },
  computed: {
    retindex: {
      get() {
        let m_i = 0;
        for (let men of this.menus) {
          if (men.href === this.$route.name) {
            return m_i;
          }
          m_i++;
        }
        return 0;
      },
      set() {},
    },
  },
};
</script>
<style>
.cusmenu {
  color: red;
}
</style>
